import React, { createElement } from 'react'
import { Button } from '../components'
import rehypeReact from 'rehype-react'
import styled from 'styled-components'
import Reaction from '../components/Reaction'

const renderAst = new rehypeReact({
  createElement,
  components: {
    button: Button
  }
}).Compiler

const Header = styled.div`
`

export default ({ data }) => {
  if(!data || !data.markdownRemark) return false
  const { markdownRemark: { excerpt, htmlAst, frontmatter } } = data
  return (
    <div>
      <Header>
        <h1>{frontmatter.title ? `${frontmatter.title}  ` : ''}{frontmatter.date}</h1>
      </Header>
      { renderAst(htmlAst) }
      { Reaction && <Reaction.component id={frontmatter.id} /> }
    </div>
  )
}
export const query = graphql`
  query PostQuery($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      excerpt
      htmlAst
      frontmatter {
        title
        subtitle
        id: date(formatString: "YYYYMMDD")
        date(formatString: "DD MMMM, YYYY")
      }
    }
  }
`



